import React, { useEffect, useState } from "react";
import Base from "./Base";
import "./core.css";
import { getProducts } from "./helper/coreAPIcalls";
import ImageHelper from "./helper/ImageHelper";
import Image from "../assets/mapImage.png";

const Home = () => {
  const direction =
    "https://www.google.com/maps/place/Umji+Aara+Machine(VINAYAK+TIMBER+MART)/@25.2329198,73.2432053,17z/data=!3m1!4b1!4m5!3m4!1s0x394290471835b43d:0x693aea120fefb0dc!8m2!3d25.232915!4d73.245394";

  const [products, setProducts] = useState([]);
  const [error, setError] = useState(false);

  const loadAllProducts = () => {
    getProducts().then((data) => {
      if (data.error) {
        setError(data.error);
      } else {
        // console.log(data);
        setProducts(data.slice(0, 6));
      }
    });
  };

  const handleCall = () => {
    window.open(`tel:+919414523569`);
  };

  useEffect(() => {
    loadAllProducts();
  }, []);

  return (
    <Base heading="Vinayak Timber Mart">
      <div className="container w-10/12 mx-auto text-black">
        <div className="flex items-center my-3">
          <p className="leading-5">
            Furniture maker in Falna <br /> Open today utill 9 p.m.
          </p>
          <a
            href={direction}
            target="_blank"
            className="btn signup-btn block text-gray-100"
          >
            Get Directions
          </a>
        </div>
        <div className="bgImage h-96"></div>

        <div className="text-center text-4xl mt-10 p-4 pt-10">Gallery</div>
        <div className="grid justify-evenly gap-8 gap-x-12 lg:grid-cols-3 md:grid-cols-3 sm:grid-cols-2">
          {products.map((product) => (
            <ImageHelper product={product} />
          ))}
        </div>

        <div className="map mt-10 pt-10">
          <div className="text-center text-4xl mt-10 p-4">Locate Us at</div>

          <a href={direction} target="_blank">
            <img src={Image} width="100%" />
          </a>
        </div>

        <div className="footer md:flex md:justify-around mt-10 pt-10">
          <div className="contact">
            <h2 className="text-2xl font-bold">Contact</h2>
            <button className="ml-1 btn login-btn block" onClick={handleCall}>
              Call Now
            </button>
            <p className="ml-1 tracking-wider">+91 9414523569</p>
          </div>
          <div className="address mt-10 md:mt-0">
            <h2 className="text-2xl font-bold">Address</h2>
            <p className="tracking-wider ml-1">
              RJ SH 16, Bus Stop, Bali road, Khudala <br /> Falna, Rajasthan,
              306116 <br /> India
            </p>
          </div>
          <div className="business mt-10 md:mt-0">
            <h2 className="text-2xl font-bold">Business Hours</h2>
            <ul className="tracking-wider ml-1">
              <li>Mon: 9:00 AM - 9:00 PM</li>
              <li>Tue: 9:00 AM - 9:00 PM</li>
              <li>Wed: 9:00 AM - 9:00 PM</li>
              <li>Thu: 9:00 AM - 9:00 PM</li>
              <li>Fri: 9:00 AM - 9:00 PM</li>
              <li>Sat: 9:00 AM - 9:00 PM</li>
              <li>Sun: 9:00 AM - 9:00 PM</li>
            </ul>
          </div>
        </div>
      </div>
    </Base>
  );
};

export default Home;
